
import { defineComponent, ref, toRefs, watchEffect } from "vue"
import { useRoute, useRouter } from "vue-router"
import { useStore } from "@/store"
import Message from "@/components/Message.vue"
import Loader from "@/components/Loader.vue"
export default defineComponent({
  components: { Message, Loader },
  setup() {
    const store = useStore(),
      route = useRoute(),
      router = useRouter()
    const email = ref(""),
      password = ref(""),
      name = ref(""),
      confirmPassword = ref(""),
      message = ref("")

    const { userInfo, loading, error } = toRefs(store.state.userRegister)
    const redirect = route.query.redirect ? route.query.redirect : "Home"

    const registerHandler = () => {
      if (
        !name.value ||
        !email.value ||
        !password.value ||
        !confirmPassword.value
      ) {
        message.value = "Fields can not be empty"
      } else if (password.value.includes(" ")) {
        message.value = "Password can not contain spaces"
      } else if (password.value.length < 6) {
        message.value = "Password must be atleast 6 characters"
      } else if (password.value !== confirmPassword.value) {
        message.value = "Passwords do not match"
      } else {
        message.value = ""
        store.dispatch("register", {
          name: name.value,
          email: email.value,
          password: password.value,
        })
      }
    }

    watchEffect(() => {
      if (userInfo.value) {
        router.push({ name: `${redirect}` })
      }
    })

    return {
      name,
      email,
      password,
      confirmPassword,
      redirect,
      loading,
      error,
      message,
      registerHandler,
    }
  },
})
